.Table {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: start;
	    align-items: flex-start;
	width: 100%;
	margin: 24px auto;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	position: relative;
	background: #666;
	overflow: hidden;
	border-radius: 8px;
	border: 4px solid #666;
}

.Cell {
	width: 0%;
	padding: 12.5%;
	cursor: pointer;
	position: relative;
	font-size: 64px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.CellContent {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 3px solid #666;
	border-radius: 8px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background: #fff;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.Cell:hover {
	/* background-color: #eee; */
}

.CellContent:empty {
	background-color: transparent;
}

@media screen and (max-width: 414px) {
	.Cell {
		font-size: 48px;
	}

	.Cell:hover {
		background-color: transparent !important;
	}
}

@media screen and (max-width: 374px) {
	.Cell {
		font-size: 32px;
	}
}

@media screen and (max-width: 319px) {
	.Cell {
		font-size: 28px;
	}
}

.move-exit-active,
.move-enter-active {
	display: none;
}

.move1-exit-active,
.move-1-exit-active,
.move4-exit-active,
.move-4-exit-active {
	display: none;
}

.move1-enter-active .CellContent:empty,
.move-1-enter-active .CellContent:empty,
.move4-enter-active .CellContent:empty,
.move-4-enter-active .CellContent:empty {
	z-index: -1;
}

.move1-enter .CellContent {
	-webkit-transform: translateX(100%);
	    -ms-transform: translateX(100%);
	        transform: translateX(100%);
}

.move-1-enter .CellContent {
	-webkit-transform: translateX(-100%);
	    -ms-transform: translateX(-100%);
	        transform: translateX(-100%);
}


.move1-enter-active .CellContent,
.move-1-enter-active .CellContent {
	-webkit-transform: translateX(0%);
	    -ms-transform: translateX(0%);
	        transform: translateX(0%);
	-webkit-transition: all .2s ease-in;
	-o-transition: all .2s ease-in;
	transition: all .2s ease-in;
}




.move4-enter .CellContent {
	-webkit-transform: translateY(100%);
	    -ms-transform: translateY(100%);
	        transform: translateY(100%);
}

.move-4-enter .CellContent {
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
}

.move4-enter-active .CellContent,
.move-4-enter-active .CellContent {
	-webkit-transform: translateY(0%);
	    -ms-transform: translateY(0%);
	        transform: translateY(0%);
	-webkit-transition: all .2s ease-in;
	-o-transition: all .2s ease-in;
	transition: all .2s ease-in;
}



